header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 60px;
    position: sticky;
    background-color: white;
    top: 0px;
    nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .logo {
            font-size: 1.8rem; }
        .burger {
            display: none; }
        ul {
            display: flex;
            list-style-type: none;
            li {
                margin: 0px 20px;
                a {
                    font-size: 1.2rem; } } } }
    .cartLink {
        widows: 30%;
        font-size: 1.2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        img {
            margin-left: 5px; } }
    .numberOfItems {
        margin-left: .4rem;
        text-align: center;
        background-color: red;
        color: white;
        width: 1.5rem;
        height: 1.5rem;
        border-radius: 50%; } }
