#special {
    display: grid;
    align-items: center;
    grid-template-columns: 50% 40%;
    grid-gap: 10%;
    height: calc( 100vh - 60px );
    .left {
        height: 60vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        .title {
            font-size: 4.8rem; }
        .desc {
            padding-top: 10%;
            padding-left: 20%; } }
    .right {
        height: 50vh;
        display: grid;
        .cards {
            overflow: hidden;
            .card {
                display: grid;
                .cardImage {
                    height: calc( 50vh - 80px );
                    width: 100%; }
                .cardText {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    height: 60px;
                    .btn {
                        cursor: pointer;
                        padding: .7rem 1.2rem;
                        border: 1px solid black; } } } }
        .navigation {
            height: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .arrows {
                width: 100px;
                display: flex;
                justify-content: space-between;
                .prev,.next {
                    cursor: pointer; } } } } }
